@import '@/style/var.less';
@import '@/style/mixins.less';

.drug-instruct-container {
  position: relative;

  .close-icon {
    width: 20px;
    height: 20px;
    display: inline-block;
    .bg-image('~@/assets/images/orderDetail/cancel.svg');
    background-position: -15px -15px;
    cursor: pointer;
    background-size: 54px 54px;
    position: absolute;
    top: 0;
    right: 0;
  }

  .instruct-title {
    font-weight: 500;
    font-size: 18px;
    color: rgba(0, 0, 0, 0.85);
    letter-spacing: 0.77px;
    margin-bottom: 45px;
  }

  .instruct-container {
    padding: 0 28px;
    max-height: 730px;
    overflow-y: auto;
  }

  .title-wrapper {
    margin-bottom: 40px;
    padding-bottom: 30px;
    position: relative;

    &::after {
      content: "";
      display: block;
      width: 100%;
      height: 1px;
      background-color: #EBEDF0;
      position: absolute;
      left: 0;
      bottom: 0;
    }

  }

  .one-title {
    font-weight: 600;
    font-size: 24px;
    color: #323233;
  }

  .small-title {
    font-weight: 600;
    font-size: 16px;
    color: @break-color2;
    line-height: 22px;
  }

  .tip {
    font-size: 14px;
    color: @break-color4;
    letter-spacing: 0;
    line-height: 16px;
  }

  .content-wrapper {
    margin-bottom: 40px;
  }

  .content-text {
    font-size: 14px;
    color: @break-color3;
    letter-spacing: 0;
    line-height: 24px;
    text-align: justify;
  }

  .mb8 {
    margin-bottom: 8px;
  }

  .mb16 {
    margin-bottom: 16px;
  }
}