@import '@/style/mixins';
@import '@/style/var';

.side-order {
  z-index: 12;
  flex-shrink: 0;
  width: 200px;
  // border-radius: 4px;
  height: calc(100vh - 64px);
  margin: -12px 12px -12px -12px;
  margin-right: 12px;
  overflow: hidden;
  background: #ffffff;
  .box-shadowed();

  .side-search {
    position: relative;
    // height: 48px;
    padding: 12px;

    &::after {
      position: absolute;
      bottom: 0;
      left: 0;
      display: block;
      width: 100%;
      height: 3px;
      background: rgba(0, 0, 0, 0.05);
      content: '';
    }

    .search-input {
      background-color: #fafafa;

      :global(.ant-input) {
        font-size: 12px;
        background-color: #fafafa;
      }

      .search-icon {
        width: 16px;
        height: 16px;
        display: inline-block;
        .bg-image('~@/assets/images/orderDetail/search_icon.svg');
        cursor: pointer;
      }
    }
  }

  .side-container {
    height: calc(100vh - 136px);
    padding: 12px 10px;
    overflow-y: auto;

    .side-item {
      position: relative;
      margin-bottom: 12px;
      padding: 10px;
      background: #ffffff;
      box-shadow: 0 1px 4px 0 rgba(0, 0, 0, 0.10);
      border-radius: 4px;
      cursor: pointer;

      // &::after {
      //   position: absolute;
      //   bottom: -12px;
      //   left: 0;
      //   display: block;
      //   width: 100%;
      //   height: 1px;
      //   background-color: @break-color5;
      //   opacity: 0.2;
      //   content: '';
      // }
    }

    .item-active {
      // outline: 1px solid @primary-color;
      // box-shadow: 2px 2px 10px 0 rgba(0, 0, 0, 0.06);
      background: #C1E3F0;

      &::after {
        display: none;
      }
    }

    .load-more {
      padding: 12px 0;
      color: @break-color5;
      font-size: 12px;
      text-align: center;
    }

    .load-data {
      position: relative;
      height: 160px;
      padding-bottom: 30px;
      background-position: center center;
      background-size: 114px;
      .bg-image('~@/assets/images/orderDetail/material.svg');

      &::after {
        position: absolute;
        bottom: 0;
        left: 50%;
        display: block;
        color: @break-color4;
        font-size: 14px;
        transform: translateX(-50%);
        content: '暂无数据';
      }
    }

    .item-user {
      display: flex;
      align-items: center;
      justify-content: space-between;
      margin-bottom: 12px;

      .user-info {
        display: flex;
        align-items: center;

        .user-avator {
          display: inline-block;
          // width: 28px;
          // height: 28px;
          border-radius: 4px;
          color: #ffffff;
          font-size: 12px;
          line-height: 18px;
          padding: 0 3px;
          text-align: center;
          // background-image: linear-gradient(136deg, #74d4eb 0%, @primary-color 100%);
          // border-radius: 14px;
          // opacity: 0.8;
        }

        .O2O {
          background-color: @primary-color;
        }

        .B2C {
          background-color: #F5A623;
        }

        .avator {
          width: 28px;
          height: 28px;
          background-repeat: no-repeat;
          background-size: contain;
          border: 2px solid #ffced9;
          border-radius: 14px;
        }

        .user-name {
          margin-left: 8px;
          color: @break-color2;
          font-weight: 500;
          font-size: 14px;
          line-height: 20px;
          letter-spacing: 0.6px;
        }
      }

      .status {
        :global(.ant-badge-status-text) {
          flex-shrink: 0;
          color: rgba(0, 0, 0, 0.65);
          font-size: 12px !important;
        }
      }
    }

    .item-price {
      display: flex;
      align-items: center;
      justify-content: space-between;
      line-height: 18px;

      .price {
        color: @break-color4;
        font-size: 12px;
      }

      .type {
        color: @primary-color;
        font-size: 12px;
        line-height: 12px;
        text-align: right;
      }
    }

    .time {
      margin-bottom: 6px;
      color: @break-color4;
      font-size: 12px;
      line-height: 18px;
    }

    .timeout {
      position: relative;
      // padding-left: 16px;
      color: @yellow-color;
      font-size: 12px;

      &::before {
        position: absolute;
        top: 50%;
        left: 0;
        display: none;
        width: 12px;
        height: 12px;
        background-image: url('~@/assets/images/orderDetail/timeout_icon.svg');
        border-radius: 50%;
        transform: translateY(-50%);
        content: '';
      }
    }

    .news {
      padding-left: 0;
      color: @primary-color;

      &::before {
        display: none;
      }
    }
  }
}