@import '@/style/mixins';
@import '@/style/var';

.delivery-log {
  padding: 20px 16px;
  background: #ffffff;
  border-radius: 4px;
  .box-shadowed();

  .shipp-info {
    position: relative;
    padding-left: 12px;

    &::before {
      position: absolute;
      top: 50%;
      left: 0;
      display: block;
      width: 4px;
      height: 14px;
      background: @primary-color;
      border-radius: 2px;
      transform: translateY(-50%);
      content: '';
    }
  }

  .manual-entry {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 22px;

    .manual-title {
      color: #217ba0;
      font-size: 14px;
      line-height: 20px;
      cursor: pointer;
    }
  }

  .log-title {
    position: relative;
    padding-left: 26px;
    color: @break-color3;
    font-weight: 500;
    font-size: 14px;
    letter-spacing: 0.4px;

    &::before {
      position: absolute;
      top: 50%;
      left: 0;
      display: block;
      width: 18px;
      height: 18px;
      transform: translateY(-50%);
      content: '';
      // background-color: @primary-color;
      .bg-image('~@/assets/images/orderDetail/delivery_log.svg');
    }
  }
}
