@import '@/style/mixins.less';
@import '@/style/var.less';

.breadcrumb-container {
  display: flex;
  align-items: center;
  margin-bottom: 12px;

  .breadcrumb-back {
    font-size: 14px;
    color: @break-color4;
    letter-spacing: 0.2px;
    padding: 8px 12px 8px 4px;
    background: #FFFFFF;
    display: flex;
    align-items: center;
    .box-shadowed();
    border-radius: 4px;
    margin-right: 12px;

    .back-icon {
      display: inline-block;
      width: 16px;
      height: 16px;
      margin-right: 3px;
    }
  }

  .cursor {
    cursor: pointer;
  }

  .breadcrumb-last {
    color: @primary-color;
  }
}