@import '@/style/mixins';
@import '@/style/var';

@mild-color: #11d2b2;
@moderate-color: #f5a623;
@severe-color: #ec808e;

.rational-advice {
  padding: 20px 16px 0 16px;
  background: #ffffff;
  border-radius: 4px;
  .box-shadowed();

  .advice-wrapper {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 20px;

    .advice-title {
      position: relative;
      margin: 0;
      padding-left: 26px;
      color: @break-color3;
      font-weight: 500;
      font-size: 14px;
      letter-spacing: 0.4px;

      &::before {
        position: absolute;
        top: 50%;
        left: 0;
        display: block;
        width: 16px;
        height: 10px;
        // background-color: @primary-color;
        .bg-image('~@/assets/images/orderDetail/ai_icon.svg');
        transform: translateY(-50%);
        content: '';
      }
    }

    .advice-tip {
      color: @break-color5;
      font-weight: 300;
      font-size: 12px;
      letter-spacing: 0;
      text-align: right;
    }
  }

  .tab {
    display: flex;
    justify-content: space-between;
    margin-bottom: 24px;
  }

  .tab-margin {
    margin-bottom: 0;
  }

  .expand-all {
    position: relative;
    // padding-right: 24px;
    color: @primary-color;
    font-weight: 500;
    font-size: 14px;
    line-height: 24px;
    letter-spacing: 0.4px;
    cursor: pointer;

    &::after {
      position: absolute;
      top: 4px;
      right: 0;
      display: none;
      width: 16px;
      height: 16px;
      background: @primary-color;
      transform: rotate(90deg);
      content: '';
    }
  }

  .tab-container {
    display: flex;
    align-items: center;

    .tab-item {
      padding-right: 30px;
      padding-bottom: 8px;
      color: @break-color3;
      font-weight: 500;
      font-size: 14px;
      line-height: 24px;
      letter-spacing: 0.4px;
      cursor: pointer;
    }

    .tab-active {
      position: relative;
      color: @primary-color;

      &::before {
        content: '';
        position: absolute;
        bottom: 0;
        left: 0;
        display: block;
        width: 40px;
        height: 4px;
        background: @primary-color;
        border-radius: 2px;
      }
    }
  }

  .advice-container {
    .advice-item {
      position: relative;
      padding-bottom: 40px;
      margin-bottom: 20px;

      &::after {
        position: absolute;
        bottom: 0;
        left: 0;
        display: block;
        width: 100%;
        height: 1px;
        background: rgba(24, 24, 24, 0.07);
        content: '';
      }

      &:last-child {
        margin-bottom: 0;
        padding-bottom: 12px;

        &::after {
          display: none;
        }
      }
    }

    .interaction-list {
      padding-bottom: 40px;

      &:last-child {
        padding-bottom: 40px;
      }
    }

    .sub-tab {
      display: flex;
      align-items: center;
      justify-content: space-between;
      margin-bottom: 16px;

      .sub-item {
        color: #333333;
        font-weight: 500;
        font-size: 16px;
        display: flex;
        align-items: center;

        .sub-change {
          display: inline-block;
          display: flex;
          align-items: center;
          justify-content: center;
          margin: 0 15px;
          width: 20px;
          border-radius: 2px;
          height: 14px;
          cursor: pointer;
          background-color: @primary-color;
        }
      }

      .advice-severity {
        .severity {
          color: #333333;
          font-size: 14px;
        }

        .status {
          padding: 2px 8px;
          color: #ffffff;
          font-size: 12px;
          line-height: 1;
          background: @mild-color;
          border-radius: 10px;
        }
      }
    }

    .tips {
      display: flex;
      margin-bottom: 24px;
      color: @severe-color;
      font-weight: 500;
      font-size: 12px;
      color: @yellow-color;
      padding: 6px 12px;
      line-height: 20px;
      background: rgba(255, 193, 0, 0.12);
      border: 1px solid @yellow-color;
      border-radius: 4px;
    }

    .small-title {
      font-weight: 500;
      font-size: 14px;
      color: @break-color2;
      letter-spacing: 0.6px;
      text-align: justify;
      margin-bottom: 6px;
    }

    .desc {
      color: @break-color3;
      font-size: 14px;
      line-height: 22px;
      letter-spacing: 0.6px;
      // text-align: justify;
    }

    .desc-ellipsis {
      .ellipsis(5);
    }

    .see-more {
      font-size: 14px;
      cursor: pointer;
      color: @primary-color;
      letter-spacing: 0.6px;
      text-align: right;
      position: absolute;
      right: 0;
      bottom: 20px;
    }

    .drug-contrain {
      display: flex;
      align-items: center;
      justify-content: space-between;
      margin-bottom: 14px;
      line-height: 24px;
    }

    .contrain-title {
      font-weight: 500;
      font-size: 16px;
      color: #333333;
      cursor: pointer;
    }

    .contrain-tip {
      font-size: 12px;
      color: @break-color5;
      letter-spacing: 0;
    }

    .title-wrapper {
      display: flex;
      align-items: center;
      line-height: 24px;
    }

    .taboo-crowd {
      color: @break-color2;
      letter-spacing: 0.6px;
      text-align: justify;
    }

    .taboo-labels {
      border: 1px solid @yellow-color;
      font-size: 12px;
      color: @yellow-color;
      line-height: 16px;
      border-radius: 10px;
      padding: 2px 8px;
      margin-right: 12px;
    }

    .repeated-list {
      margin-bottom: 20px;
      padding-bottom: 10px;
      position: relative;

      &::after {
        position: absolute;
        bottom: 0;
        left: 0;
        display: block;
        width: 100%;
        height: 1px;
        background: rgba(24, 24, 24, 0.07);
        content: '';
      }

      &:last-child {
        padding-bottom: 0;
        margin-bottom: 0;

        &::after {
          display: none;
        }
      }
    }

    .avoid-list {
      margin-bottom: 20px;
      padding-bottom: 40px;
      position: relative;

      .see-more {
        bottom: 20px;
      }

      &::after {
        position: absolute;
        bottom: 0;
        left: 0;
        display: block;
        width: 100%;
        height: 1px;
        background: rgba(24, 24, 24, 0.07);
        content: '';
      }

      &:last-child {
        padding-bottom: 20px;
        margin-bottom: 0;

        .see-more {
          bottom: 0;
        }

        &::after {
          display: none;
        }
      }
    }

    .repeated-medication {
      display: flex;
      justify-content: space-between;
      margin-bottom: 12px;
    }

    .repeated-title {
      padding-left: 18px;
      line-height: 24px;
      cursor: pointer;
      position: relative;

      &::before {
        content: "";
        display: block;
        width: 6px;
        height: 6px;
        border-radius: 50%;
        background: @yellow-color;
        position: absolute;
        left: 0;
        top: 50%;
        transform: translateY(-50%);
      }
    }

    .repeated-small-title {
      font-weight: 500;
      color: @break-color2;
      letter-spacing: 0.6px;
      text-align: justify;
    }

    .mb6 {
      margin-bottom: 6px;
    }

    .mb10 {
      margin-bottom: 10px;
    }

    .no-data-wrapper {
      width: 100%;
      padding-bottom: 40px;
      display: flex;
      align-items: center;
      justify-content: center;
      flex-direction: column;

      .no-data-icon {
        width: 160px;
        height: 160px;
        .bg-image('~@/assets/images/orderDetail/function.svg')
      }

      .no-search-icon {
        width: 160px;
        height: 160px;
        .bg-image('~@/assets/images/orderDetail/search_no_icon.svg')
      }

      .error-icon {
        width: 160px;
        height: 160px;
        .bg-image('~@/assets/images/orderDetail/wifi_icon.svg')
      }

      .no-data-title {
        color: #969799;
        text-align: center;
        line-height: 20px;
        font-size: 14px;
      }
    }
  }
}

.rational-padding {
  padding-bottom: 20px;
}

:global(.ant-popover-inner-content) {
  width: 478px;

  .popover-content {
    .popover-title {
      position: relative;
      margin-bottom: 12px;
      padding-left: 12px;
      color: @break-color3;
      font-weight: 500;
      font-size: 14px;

      &::after {
        position: absolute;
        top: 50%;
        left: 0;
        display: block;
        width: 4px;
        height: 14px;
        background: @primary-color;
        border-radius: 2px;
        transform: translateY(-50%);
        content: '';
      }
    }

    .popover-tip {
      margin-bottom: 6px;
      color: @break-color3;
      font-size: 12px;
      line-height: 18px;
      text-align: justify;
    }

    .popover-desc {
      color: @severe-color;
      font-size: 12px;
      line-height: 18px;
      text-align: justify;
    }
  }
}

:global(.overlay-tool-tip) {
  .tool-tip-tit {
    display: flex;
    padding: 15px 10px;

    .tip-tit-item {
      width: 40px;
      height: 20px;
      margin-right: 10px;
      color: #ffffff;
      font-size: 12px;
      line-height: 20px;
      text-align: center;
      background-color: @moderate-color;
      border-radius: 10px;

      &:first-child {
        background: @mild-color;
      }

      &:last-child {
        margin-right: 0;
        background: @severe-color;
      }
    }
  }
}