@import '@/style/mixins';
@import '@/style/var';

.order-status-container {
  min-width: 774px;
  padding: 16px 20px 12px;
  background: #ffffff;
  border-radius: 4px;
  .box-shadowed();

  .status-tool {
    display: flex;
    justify-content: space-between;
    padding-bottom: 6px;

    .status-info {
      display: flex;
      align-items: center;
      color: @break-color3;
      font-weight: 500;
      font-size: 14px;
      line-height: 20px;
      letter-spacing: 0.2px;

      .status-no {
        color: @primary-color;
      }

      .status-time {
        position: relative;
        margin-left: 15px;
        padding-left: 15px;
        color: @break-color4;
        font-size: 12px;
        line-height: 20px;

        &::before {
          position: absolute;
          top: 50%;
          left: 0;
          display: block;
          width: 1px;
          height: 14px;
          background-color: @break-color4;
          transform: translateY(-50%);
          content: '';
        }
      }
    }

    .order-status {
      height: 24px;
      padding: 0 10px 0 10px;
      color: #ffffff;
      font-weight: 500;
      font-size: 12px;
      line-height: 24px;
      // border: 1px solid @error-color;
      border-radius: 12px;
      cursor: pointer;
    }

    .wait,
    .finished,
    .received {
      background-image: linear-gradient(180deg, #67B7E2 0%, #217BA0 100%);
    }

    .delivery_wait {
      background-image: linear-gradient(180deg, #FFCE8A 0%, #FF8147 100%);
    }

    .delivering,
    .begin_delivery {
      background-image: linear-gradient(180deg, #FFCE8A 0%, #FF8147 100%);
    }

    .timeout,
    .delivery_exception,
    .rejected {
      background-image: linear-gradient(180deg, #FF8585 0%, #FF5656 100%);
    }

    .expired {
      background-image: linear-gradient(180deg, #EBEBEB 0%, #A7A7A7 100%);
    }

    .order-timeout {
      // padding: 0 20px 0 30px;
      position: relative;

      &::before {
        position: absolute;
        top: 50%;
        left: 10px;
        display: none;
        width: 12px;
        height: 12px;
        // background-color: rgb(221, 221, 221);
        background-image: url('~@/assets/images/orderDetail/timeout_icon.svg');
        border-radius: 50%;
        transform: translateY(-50%);
        content: '';
      }
    }
  }

  .step-container {
    display: flex;
    margin: 0;

    .step-item {
      position: relative;
      flex: 1;
      box-sizing: border-box;
      height: 82px;
      padding: 25px 0 0 46px;

      &::before {
        position: absolute;
        top: 0;
        left: 0;
        z-index: 2;
        display: block;
        width: 40px;
        height: 40px;
        background-color: rgba(0, 0, 0, 0.1);
        border-radius: 50%;
        content: '';
      }

      &::after {
        position: absolute;
        top: 20px;
        left: 40px;
        z-index: 1;
        display: block;
        width: calc(100% - 40px);
        border-top: 2px dashed;
        border-top-color: #e5e5e5;
        content: '';
      }

      &:nth-child(1) {
        .item-icon {
          .bg-image('~@/assets/images/orderDetail/submit_icon.svg');
          background-size: 20px 24px;
        }
      }

      &:nth-child(2) {
        .item-icon {
          .bg-image('~@/assets/images/orderDetail/prescript_icon.svg');
          background-size: 34px 34px;
        }
      }

      &:nth-child(3) {
        .item-icon {
          .bg-image('~@/assets/images/orderDetail/logistic_icon.svg');
          background-size: 29px 22px;
        }
      }

      &:nth-child(4) {
        .step-name {
          width: 160px;
        }

        .item-icon {
          .bg-image('~@/assets/images/orderDetail/receipt_icon.svg');
          background-size: 22px 23px;
        }
      }

      &:last-child::after {
        display: none;
      }

      .item-icon {
        position: absolute;
        top: 0;
        left: 0;
        z-index: 9;
        display: inline-block;
        width: 40px;
        height: 40px;
        background-position: center center;
      }

      .step-title {
        color: @break-color4;
        font-weight: 500;
        font-size: 12px;
        line-height: 18px;
        letter-spacing: 0.34px;
      }

      .step-time {
        color: @break-color3;
        font-weight: 300;
        font-size: 12px;
        line-height: 18px;
        letter-spacing: 0.34px;
      }

      .step-name {
        position: absolute;
        // width: 190px;
        width: 100%;
        height: 20px;
        color: @break-color3;
        font-weight: 300;
        font-size: 12px;
        line-height: 18px;
        letter-spacing: 0.34px;
        .ellipsis(1);
      }

      // .error-name {
      //   color: @error-color;
      // }
    }

    .step-active {
      .step-title {
        color: @primary-color;
      }

      &::before {
        background: linear-gradient(136deg, #74d4eb 0%, @primary-color 100%);
      }
    }

    .step-error {
      &::before {
        background: rgba(0, 0, 0, 0.4);
      }
    }

    .step-finsh {
      &::after {
        // border-color: @primary-color;
        border-top: 2px solid @primary-color;
      }
    }
  }
}