@import '@/style/mixins';
@import '@/style/var';

.perscript-info {
  width: 430px;
  // height: 492px;
  height: calc(100vh - 432px);
  padding: 16px 0 20px;
  background: #ffffff;
  border-radius: 4px;
  .box-shadowed();

  .perscript-title {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 12px;
    padding: 0 20px;

    .perscript-label {
      color: @break-color3;
      font-weight: 500;
      font-size: 14px;
      letter-spacing: 0.2px;
    }

    .perscript-no {
      color: @primary-color;
      font-weight: 500;
      font-size: 14px;
      letter-spacing: 0.2px;
    }

    .perscript-status {
      display: inline-block;
      margin-left: 8px;
      padding: 3px 5px;
      color: @primary-color;
      font-size: 12px;
      line-height: 1;
      text-align: center;
      border: 1px solid #46a0c0;
      border-radius: 4px;
    }
  }

  .perscript-hosip {
    position: relative;
    padding-right: 38px;
    color: @break-color3;
    font-size: 12px;
    line-height: 20px;
    letter-spacing: 0.4px;

    &::after {
      position: absolute;
      top: -5px;
      right: 0;
      display: block;
      width: 30px;
      height: 30px;
      background: #e6eefd;
      border-radius: 50%;
      content: '';
    }
  }

  .diagnosis-wrapper {
    display: flex;
    justify-content: space-between;
    margin-bottom: 7px;
    padding: 0 20px;
    color: @break-color2;
    font-size: 12px;
    line-height: 30px;
    letter-spacing: 0.4px;

    .diagnosis {
      position: relative;
      height: 30px;
      padding: 0 12px;
      background: #f3f8fb;

      &::before {
        position: absolute;
        top: 0;
        left: 0;
        display: block;
        width: 4px;
        height: 100%;
        background: #46a0c0;
        content: '';
      }
    }
  }

  .perscript-desc {
    display: flex;
    justify-content: space-between;
    margin-bottom: 20px;
    padding: 0 20px;
    color: @break-color3;
    font-size: 12px;
    line-height: 20px;
    letter-spacing: 0.4px;

    .desc-label {
      margin-left: 20px;
    }

    .desc-value {
      color: @primary-color;
      font-weight: 500;
      letter-spacing: 0.17px;
    }
  }
}

.perscript-info-calc {
  height: calc(100vh - 377px);
}