@import '@/style/mixins';
@import '@/style/var';

.prescript-preview {
  position: relative;
  display: flex;

  &::before {
    position: absolute;
    top: 0;
    left: 0;
    display: block;
    width: 100%;
    height: 2px;
    background: rgba(0, 0, 0, 0.04);
    content: '';
  }

  .container {
    flex: 1;
    height: 367px;

    .prescript-tools {
      position: relative;
      display: flex;
      flex: 1;
      align-items: center;
      justify-content: space-between;
      padding: 12px 20px;

      &::before {
        position: absolute;
        bottom: 0;
        left: 0;
        display: block;
        width: 100%;
        height: 2px;
        background: rgba(0, 0, 0, 0.04);
        content: '';
      }
    }

    .enlarge {
      position: relative;
      // padding-left: 14px;
      color: rgba(0, 0, 0, 0.35);
      line-height: 14px;
      letter-spacing: 0;
      cursor: pointer;

      &::before {
        position: absolute;
        top: 50%;
        left: 0;
        display: none;
        width: 14px;
        height: 14px;
        background: @break-color5;
        transform: translateY(-50%);
        content: '';
      }
    }

    .full-screen {
      margin-left: 5px;
      color: rgba(0, 0, 0, 0.35);
    }

    .enlarge-number {
      padding: 0 6px;
      color: rgba(0, 0, 0, 0.35);
      font-size: 12px;
      letter-spacing: 0;
      text-align: center;
    }

    .tool {
      display: flex;
      align-items: center;

      .download {
        position: relative;
        display: flex;
        align-items: center;
        width: 18px;
        height: 18px;
        margin-right: 15px;
        color: @break-color5;
        cursor: pointer;

        &::before {
          position: absolute;
          top: 50%;
          left: 0;
          display: none;
          width: 14px;
          height: 14px;
          background: @break-color5;
          transform: translateY(-50%);
          content: '';
        }
      }

      .print {
        position: relative;
        display: flex;
        align-items: center;
        width: 18px;
        height: 18px;
        color: @break-color5;
        cursor: pointer;

        &::before {
          position: absolute;
          top: 50%;
          left: 0;
          display: none;
          width: 14px;
          height: 14px;
          background: @break-color5;
          transform: translateY(-50%);
          content: '';
        }
      }
    }

    .prescript-container {
      // width: 296px;
      width: 403px;
      max-width: 100%;
      // height: 300px;
      height: calc(100vh - 632px);
      margin: 20px 13px 0 13px;
      overflow-y: scroll;
      .box-shadowed();

      &:hover {
        &::-webkit-scrollbar {
          display: block;
        }
      }

      &::-webkit-scrollbar {
        display: none;
        width: 4px;
        height: 4px;
        background-color: #f5f5f5;
      }

      &::-webkit-scrollbar-thumb {
        background-color: #cdcdcd;
        border-radius: 10px;
        opacity: 0.6;
      }
    }

    .prescript-container-calc {
      height: calc(100vh - 577px);
    }
  }

  .prescript-change {
    display: flex;
    flex-direction: column;
    width: 108px;
    height: 394px;
    margin: 0;
    padding: 17px 11px;
    overflow-y: auto;
    background: #fafafa;

    &:hover {
      &::-webkit-scrollbar {
        display: block;
      }
    }

    &::-webkit-scrollbar {
      display: none;
      width: 4px;
      height: 0;
      background-color: #f5f5f5;
    }

    &::-webkit-scrollbar-thumb {
      background-color: #cdcdcd;
      border-radius: 10px;
      opacity: 0.6;
    }

    .change-item {
      position: relative;
      flex-shrink: 0;
      width: 80px;
      height: 110px;
      margin-bottom: 14px;
      overflow: hidden;
      border-radius: 4px;
      cursor: pointer;
      .box-shadowed();

      .item-page {
        position: absolute;
        right: -1px;
        bottom: -1px;
        display: none;
        width: 10px;
        height: 18px;
        color: #ffffff;
        font-weight: 600;
        font-size: 12px;
        letter-spacing: 0;
        text-align: center;
        background: #4d95b3;
        border-radius: 3px 0 3px 0;
      }

      &:last-child {
        margin-bottom: 0;
      }
    }

    .change-active {
      border: 1.5px solid rgba(33, 123, 160, 0.8);

      .item-page {
        display: block;
      }
    }
  }

  .react-pdf__message--loading {
    display: flex;
    align-items: center;
  }
}