@import '@/style/mixins';
@import '@/style/var';

.user-container {
  position: relative;
  display: flex;
  align-items: center;
  padding: 15px 20px;
  background: #ffffff;
  border-radius: 4px;
  .box-shadowed();

  .user-avator {
    display: flex;
    flex-shrink: 0;
    flex-shrink: 0;
    align-items: center;
    justify-content: center;
    width: 50px;
    height: 50px;
    font-size: 24px;
    color: #FFFFFF;
    // background-color: rgb(231, 231, 231);
    background-image: linear-gradient(136deg, #74D4EB 0%, #217BA0 100%);
    border-radius: 4px;
  }

  .user-info {
    display: flex;
    margin-bottom: 0;
    padding-left: 12px;

    .user-item {
      display: flex;
      flex-direction: column;
      flex-shrink: 0;
      justify-content: space-between;
      width: 130px;
      padding: 4px 0 2px 0;

      .user-name {
        color: @break-color2;
        font-weight: 500;
        font-size: 16px;
        letter-spacing: 0.6px;
        margin-bottom: 4px;
      }

      .user-age {
        position: relative;
        // padding-left: 15px;
        color: @break-color4;
        font-size: 12px;
        line-height: 22px;
        letter-spacing: 0;

        &::before {
          position: absolute;
          top: 50%;
          left: 0;
          display: none;
          width: 12px;
          height: 12px;
          background-color: rgb(224, 224, 224);
          transform: translateY(-50%);
          content: '';
        }
      }
    }

    .user-desc {
      position: relative;
      display: flex;
      flex-direction: column;
      flex-shrink: 0;
      justify-content: space-between;
      margin-right: 40px;
      padding: 4px 0 2px 0;

      &::after {
        position: absolute;
        top: 0;
        right: 0;
        display: block;
        width: 1px;
        height: 100%;
        background-color: rgba(0, 0, 0, 0.1);
        content: '';
      }

      &:last-child::after {
        display: none;
      }

      &:nth-child(2) {
        width: 125px;
      }

      &:nth-child(3) {
        width: 175px;
      }

      &:nth-child(4) {
        width: 125px;
      }

      .desc-label {
        color: @break-color4;
        font-size: 12px;
        line-height: 14px;
        letter-spacing: 0;
      }

      .desc-value {
        display: flex;
        align-items: center;
        color: @break-color2;
        font-size: 14px;
        line-height: 22px;
        letter-spacing: 0.4px;

        .to-address {
          width: 150px;
          .ellipsis(1);
        }
      }

      .allergen {
        .ellipsis(1);
      }
    }
  }

  .user-more {
    position: absolute;
    top: 27px;
    right: 27px;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 25px;
    height: 25px;
    // background-color: rgb(212, 212, 212);
    cursor: pointer;
  }
}