@import '@/style/mixins';
@import '@/style/var';

.pay-container {
  position: relative;
  display: flex;
  justify-content: space-between;
  box-sizing: border-box;
  width: 430px;
  height: 140px;
  padding: 16px 20px;
  background: #ffffff;
  border-radius: 4px;
  .box-shadowed();

  &::before {
    position: absolute;
    top: 0;
    left: 60%;
    display: block;
    width: 16px;
    height: 8px;
    background-color: #f0f2f5;
    border-radius: 0 0 50px 50px;
    box-shadow: inset 0 -1px 4px 0 rgb(0 21 41 / 12%);
    content: '';
  }

  &::after {
    position: absolute;
    bottom: 0;
    left: 60%;
    display: block;
    width: 16px;
    height: 8px;
    background-color: #f0f2f5;
    border-radius: 50px 50px 0 0;
    box-shadow: inset 0 4px 4px 0 rgb(0 21 41 / 12%);
    content: '';
  }

  .order-info {
    flex: 1;
    padding-right: 25px;

    .order-title {
      position: relative;
      margin-bottom: 15px;
      padding-left: 32px;
      color: @break-color3;
      font-weight: 500;
      font-size: 14px;
      letter-spacing: 0.2px;

      &::before {
        position: absolute;
        top: 50%;
        left: 0;
        display: block;
        width: 20px;
        height: 20px;
        border-radius: 50%;
        transform: translateY(-50%);
        content: '';
        // background-color: rgb(230, 230, 230);
        .bg-image('~@/assets/images/orderDetail/pay_info.svg');
      }
    }

    .container {
      .info-item {
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin-bottom: 4px;

        .item-label {
          color: @break-color4;
          font-size: 12px;
          line-height: 20px;
          letter-spacing: 0.4px;
        }

        .item-value {
          color: @break-color3;
          font-size: 12px;
          line-height: 20px;
          letter-spacing: 0.4px;
          text-align: right;

          .item-tip {
            margin-right: 5px;
            color: #ffa000;
          }
        }
      }
    }
  }

  .pay-info {
    display: flex;
    flex-direction: column;
    width: 144px;
    padding-left: 20px;
    border-left: 2px dashed #f0f2f5;

    .pay-status {
      align-self: flex-end;
      // width: 54px;
      margin-bottom: 11px;
      padding: 6px 8px;
      color: #ffffff;
      font-weight: 500;
      font-size: 12px;
      line-height: 10px;
      background: #67b475;
      border: 1px solid #aff4bc;
      border-radius: 12px;
    }

    .pay-title {
      margin-bottom: 13px;
      color: @break-color4;
      font-size: 12px;
      line-height: 20px;
      letter-spacing: 0.4px;
    }

    .pay-price {
      color: @break-color3;
      font-size: 32px;
      line-height: 32px;
      letter-spacing: 1px;
    }
  }
}