@import '@/style/mixins';
@import '@/style/var';

.drug-container {
  position: relative;
  padding: 14px 16px 16px;
  overflow: hidden;
  background: #ffffff;
  border-radius: 4px;
  .box-shadowed();

  &::after {
    position: absolute;
    top: -15px;
    right: -15px;
    z-index: 0;
    display: block;
    width: 155px;
    height: 155px;
    background-repeat: no-repeat;
    background-position: 0;
    border-radius: 50%;
    // background-color: rgba(188, 215, 226, 0.5);
    transform: rotate(-30deg);
    content: '';
  }

  .price-title {
    position: relative;
    z-index: 1000;
    color: @primary-color;
    font-weight: 500;
    font-size: 14px;
    text-align: right;
    cursor: pointer;
  }

  .drug-wrapper {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 16px;

    .drug-price {
      color: rgba(0, 0, 0, 0.65);
      font-weight: 500;
      font-size: 14px;
      letter-spacing: 0.4px;
    }
  }

  .drug-title {
    position: relative;
    padding-left: 16px;
    color: @break-color3;
    font-weight: 500;
    font-size: 14px;
    line-height: 20px;
    letter-spacing: 0.4px;

    &::before {
      position: absolute;
      top: 0;
      left: 0;
      display: block;
      width: 4px;
      height: 100%;
      background: rgba(33, 123, 160, 0.8);
      border-radius: 2px;
      content: '';
    }
  }

  .drug-close {
    display: flex;
    align-items: center;
    height: 48px;
    margin-bottom: 12px;
    padding: 14px 16px;
    color: @break-color2;
    font-size: 14px;
    background: #bcd7e2;

    .close-title {
      position: relative;
      margin-right: 27px;
      // padding-right: 25px;
      color: @break-color2;
      font-weight: 500;
      font-size: 14px;
      cursor: pointer;

      &::after {
        position: absolute;
        top: 50%;
        right: 0;
        display: none;
        width: 20px;
        height: 20px;
        background-color: @break-color4;
        border-radius: 50%;
        transform: translateY(-50%);
        content: '';
      }
    }

    .close-info {
      display: flex;
      align-items: center;
    }

    .close-drug {
      display: -webkit-box;
      max-width: 280px;
      padding-right: 3px;
      overflow: hidden;
      text-overflow: ellipsis;
      -webkit-line-clamp: 1;
      -webkit-box-orient: vertical;
    }
  }

  .ellipsis {
    .ellipsis(2);
  }
}

.received,
.begin_delivery,
.delivering,
.delivery_exception,
.finished,
.delivery_wait {
  &::after {
    background-image: url('~@/assets/images/orderDetail/complete_seal.svg');
  }
}

.rejected {
  &::after {
    background-image: url('~@/assets/images/orderDetail/reject_seal.svg');
  }
}

:global {
  .price-wrapper {
    .ant-tooltip-inner {
      position: relative;
      padding-left: 20px;
      color: #ffffff;
      font-weight: 500;
      font-size: 12px;

      &::after {
        position: absolute;
        top: 50%;
        left: 10px;
        display: block;
        width: 4px;
        height: 14px;
        background: #46a0c0;
        border-radius: 2px;
        transform: translateY(-50%);
        content: '';
      }
    }
  }
}